
(function() {
  'use strict';

  angular
    .module('EVQueue', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ui.bootstrap','ui.router']);
})();

(function() {
  'use strict';

  angular
    .module('EVQueue')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($log) {
    //var vm = this;
    $log.log("Controller Init")

  }
})();

(function() {
    'use strict';

    angular
        .module('EVQueue')
        .controller('LoginController', MainController);

    /** @ngInject */
    function MainController($log) {
        //var vm = this;
        $log.log("LoginController Init")

    }
})();

(function() {
  'use strict';

  angular
    .module('EVQueue')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('EVQueue')
    .constant('AppName', 'EVQueue');

})();

(function () {
    'use strict';

    angular
        .module('EVQueue')
        .config(config);

    /** @ngInject */
    function config($logProvider, $stateProvider) {
        // Enable log
        $logProvider.debugEnabled(true);

        $stateProvider
            .state('login', {
                url: '/login',
                templateUrl: 'login/login.html',
                controller: 'LoginController',
                controllerAs: 'vm'
            })
            .state('queue', {
                url: '/queue',
                templateUrl: 'login/queue.html',
                controller: 'QueueController',
                controllerAs: 'vm'
            })
            .otherwise('/queue');

    }

})();

angular.module("myNewProject").run(["$templateCache", function($templateCache) {$templateCache.put("app/login/login.html","LOGIN");
$templateCache.put("app/queue/queue.html","LIST HERE");}]);